import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import SocialMediaPostDescription from "../../../components/social_media_post_design_components/social_media_post_description/SocialMediaPostDescription";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"

import { useLocation } from "react-router-dom"

import SocialMediaPostDesignBanner from "./SocialMediaDesignBanner.jpg";

export default function SocialMediaPostDesign() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="social-media-post-design">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="Social Media Post Design"
        bannerParagraph="Capture attention and inspire engagement with our eye-catching social media post design!"
        bannerButton1="LEARN MORE"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={SocialMediaPostDesignBanner}
      />

      <SubCategoriesCardLeft
        paragrapgh="The talented team of our graphic design agency will craft a visually captivating design that combines the perfect blend of aesthetics and impact. From vibrant color schemes to striking typography, every element is carefully curated to make your post stand out in the crowded digital landscape. Don't let your social media presence blend into the noise. "
        button="LET'S CHAT"
        picture={pic1}
      />

      <SubCategoriesCardRight
        paragraph="Let our social media post-designing expertise elevate your online content and drive engagement like never before. Get ready to receive those likes, comments, and shares as your audience can't resist interacting with your visually stunning posts!"
        button="LET'S CHAT"
        picture={pic2}
      />

      <SocialMediaPostDescription/>

      <br />
      <br />
      <br />
      <br />

      <Pricing />

      <br />
      <br />
      <br />

      <Skills />

      <br />
      <br />
      <br />

      <Ourwork />

      <Footer />
    </div>
  );
}
